import Vue from 'vue';

const FcTree =  {
    name: 'FcTree',
    props: ['param', 'callback'],
    data() {
		return {
            propertyObj: {},
            relativeObj: {},
            renderArr: [],
            lazyRenderArr: [],
            showArr: [],
            rendStart: 0,
            settingId: 0,
        };
	},
    created() {
        this.init();
    },
    methods: {
        clickAction(e) {
            const { propertyObj, relativeObj} = this;
            const id = e.target.getAttribute('data-id');
            const icon = e.target.getAttribute('data-icon');
            this.param.checkedId = id;
            if (icon && relativeObj[id]) {
                this.changeAction(propertyObj[id]);
            } else {
                this.linkAction(propertyObj[id]);
            }
        },
        changeAction(param, open) {
            const { propertyObj, relativeObj} = this;
            propertyObj[param.id].open = open || !propertyObj[param.id].open;
            this.setOpen(propertyObj[param.id]);
            this.setRenderArr();
        },
        linkAction(param) {
            const { propertyObj, relativeObj} = this;
            if (relativeObj[param.id]) {
                this.changeAction(propertyObj[param.id], true);
            }
            if (param.href) {
                location.href = param.href;
            }
        },
        setOpen(param) {
            if (!this.param.catch) {
                return;
            }
            const data = this.param.data || [];
            for(var i in data) {
                if (data[i].id == param.id) {
                    data[i].open == param.open;
                }
            }
            if (this.callback) {
                this.callback({
                    type: 'changeOpen'
                });
            }
        },
        init() {
            const data = this.param.data || [];
            if (!data || !data.length) {
                return;
            }
            //var start = new Date().getTime();
            const relativeObj = this.relativeObj;
            for(var i in data) {
                this.propertyObj[data[i].id] = data[i];
                this.propertyObj[data[i].id].show = (this.param.showAll || this.param.rootId == data[i].parentId) ? true : (data[i].show || false);
                if (data[i].open) {
                    this.propertyObj[data[i].id].open = data[i].open;
                }
                relativeObj[data[i].parentId] = relativeObj[data[i].parentId] || [];
                if(!data[i].remove) {
                    relativeObj[data[i].parentId].push(data[i].id);
                }
            }
            //var end = new Date().getTime();
            //console.log('init wait' + (end - start));
            this.setRenderArr();
            setTimeout(() => {
                const scroll = this.$refs.scroll;
                if (scroll && !this.isSetScroll) {
                    scroll.onscroll = () => {
                        this.checkScroll();
                        this.hideSetting();
                    }
                    this.isSetScroll = true;
                }
            });
        },
        setRenderArr() {
            const relativeObj = this.relativeObj;
            const loopKeys = {};
            let loopId = this.param.rootId;
            loopKeys[loopId] = 0;
            let loopObj = relativeObj[loopId];
            let num = 0;
            let level = 0;
            this.renderArr = [];
            //var start = new Date().getTime();
            const renderArr = [];
            while(loopKeys[this.param.rootId] <= relativeObj[this.param.rootId].length && num < 1000000) {
                loopKeys[loopId] = loopKeys[loopId] || 0;
                const pushId = loopObj[loopKeys[loopId]];
                if (pushId) {
                    if (this.propertyObj[this.propertyObj[pushId].parentId] && !this.propertyObj[this.propertyObj[pushId].parentId].open) {
                        this.propertyObj[pushId].open = false;
                        this.propertyObj[pushId].hide = true;
                    } else {
                        this.propertyObj[pushId].show = true;
                        this.propertyObj[pushId].hide = false;
                    }
                    if (this.propertyObj[pushId].show && !this.propertyObj[pushId].hide) {
                        renderArr.push(pushId);
                    }
                    this.propertyObj[pushId].level = level;
                    if (!relativeObj[pushId]) {
                        loopKeys[loopId] ++;
                    }
                }
                if (relativeObj[pushId]) {
                    if (this.propertyObj[pushId].open == undefined && this.param.showAll) {
                        this.propertyObj[pushId].open = true;
                    }
                    loopObj = relativeObj[pushId];
                    loopId = pushId;
                    level ++;
                } else if (loopKeys[loopId] >= loopObj.length) {
                    loopId = this.propertyObj[loopId] ? this.propertyObj[loopId].parentId : this.param.rootId;
                    loopObj = relativeObj[loopId];
                    loopKeys[loopId] ++;
                    if (loopId == this.param.rootId) {
                        level = 0;
                    } else {
                        level --;
                    }
                }
                num++;
            }
            //var end = new Date().getTime();
            //console.log('setRenderArr wait' + (end - start));
            this.renderArr = renderArr;
            console.log(this.renderArr.length);
            this.setLazyRenderArr();
        },
        setLazyRenderArr() {
            if (!this.param.lazy || !this.renderArr || !this.param.height) {
                this.showArr = this.renderArr;
                return;
            }
            setTimeout(() => {
                const scroll = this.$refs.scroll;
                const scrollContent = this.$refs.scrollContent;
                if (!scroll || !scrollContent) {
                    this.showArr = this.renderArr;
                    return;
                }
                const scrollHeight = this.param.height;
                const itemHeight = this.param.itemHeight || 40;
                scrollContent.style.height = (this.renderArr.length) * itemHeight + 'px';
                //scroll.scrollTop = 5200;
                let scrollTop = scroll.scrollTop;
                this.rendStart = scrollTop - scrollHeight * 2;
                this.rendStart = this.rendStart > 0 ? this.rendStart : 0;
                this.rendEnd = this.rendStart + scrollHeight * 5;
                let startKey = parseInt(this.rendStart/itemHeight);
                startKey = startKey > 0 ? startKey : 0;
                let endKey = parseInt(this.rendEnd/itemHeight);
                endKey = endKey < this.renderArr.length ? endKey : this.renderArr.length;
                this.lazyRenderArr = [];
                for (var i = startKey; i < endKey; i++) {
                    this.lazyRenderArr.push(this.renderArr[i]);
                }
                this.showArr = this.lazyRenderArr;
            });
        },
        checkScroll() {
            const scroll = this.$refs.scroll;
            const scrollHeight = this.param.height;
            let scrollTop = scroll.scrollTop;
            if (scrollTop > this.rendEnd - scrollHeight || scrollTop < this.rendStart + scrollHeight) {
                this.setLazyRenderArr();
            }
        },
        scrollClick(e) {
            const target = e.target.getAttribute('data-target');
            let hideSetting = true;
            switch(target) {
                case 'icon':
                    this.clickAction(e);
                    break;
                case 'setting':
                    hideSetting = false;
                    this.changeSetting(e);
                    break;
                case 'add':
                case 'rename':
                case 'move':
                case 'remove':
                    hideSetting = false;
                    this.callback({
                        type: target,
                        settingId: this.settingId,
                    });
                    break;
            }
            if (hideSetting) {
                this.hideSetting();
            }
        },
        changeSetting(e) {
            const id = e.target.getAttribute('data-id');
            this.settingId = id;
            //console.log(this.settingId);
            if (this.settingShow) {
                this.hideSetting();
                clearTimeout(this.csTo);
                this.csTo = setTimeout(() => {
                    this.showSetting(e);
                }, 400);
            } else {
                this.showSetting(e);
            }
        },
        showSetting(e) {
            const key = e.target.getAttribute('data-key');
            const settingBar = this.$refs.settingBar;
            const itemHeight = this.param.itemHeight || 40;
            settingBar.style.display = 'block';
            clearTimeout(this.sTo);
            this.sTo = setTimeout(() => {
                settingBar.style.top = this.rendStart + parseInt(key) * itemHeight + 'px';
                settingBar.style.left = e.target.offsetLeft + 25 + 'px';
                settingBar.style.width = this.relativeObj[this.settingId] ? '200px' : '245px';
                settingBar.style.opacity = 1;
                this.settingShow = true;
            });
        },
        hideSetting() {
            if (!this.settingShow) {
                return;
            }
            const settingBar = this.$refs.settingBar;
            settingBar.style.width = 0;
            settingBar.style.opacity = 0;
            clearTimeout(this.hTo);
            this.hTo = setTimeout(() => {
                settingBar.style.display = 'none';
                this.settingShow = false;
            }, 400);
        },
    }
}

export default FcTree;


