import Vue from 'vue';
import { mapState } from 'vuex';

import FcMenuStore from './store';
import FcTree from 'fcbox/tree/index.vue';

const FcMenu =  {
    name: 'FcMenu',
    data() {
		return {};
	},
    components: {
        FcTree
    },
    computed: Object.assign ({
            
        }, mapState({
            menuParam: (state) => state.FcMenu.menuParam,
		})
	),
    created() {
        if (!this.$store.state.FcMenu) {
            this.$store.registerModule('FcMenu', FcMenuStore);
        }
        this.$store.dispatch('checkStore', {
            entry: 'index',
            state: ['FcMenu']
        });
    },
    methods: {
        treeCallback(data) {
            if (data.type == 'changeOpen') {
                this.$store.dispatch('setStore', {
                    entry: 'index',
                    state: ['FcMenu']
                });
            }
        }
    }
}

export default FcMenu;


