<template>
    <div class="FcTree">
        <div class="themeMenu" v-if="param.theme == 'menu'">
            <div
                v-for="(item, key) in renderArr"
                class="item clearFix"
                :style="{paddingLeft: propertyObj[item].level * 20 + 10 + 'px'}"
                @click="clickAction"
                :data-id="item"
            >
                <div class="icon" :data-id="item" :data-icon="1" :class="{open: propertyObj[item].open, text: !relativeObj[item] || !relativeObj[item].length}"></div>
                <div class="title" :data-id="item">{{propertyObj[item].name}}</div>
            </div>
        </div>
        <div class="themeSetList" v-if="param.theme == 'setList'" ref="scroll" @click="scrollClick">
            <div ref="scrollContent" class="scrollContent">
                <div class="settingBar" ref="settingBar">
                    <span data-target="add">新增子类</span>
                    <span data-target="rename">重命名</span>
                    <span data-target="move">迁移</span>
                    <span data-target="remove" v-if="!relativeObj[settingId] || !relativeObj[settingId].length">作废</span>
                </div>
                <div
                        v-for="(item, key) in showArr"
                        class="item clearFix"
                        :style="{paddingLeft: propertyObj[item].level * 20 + 10 + 'px', top: rendStart + key * 40 + 'px'}"
                        :data-id="item"
                >
                    <div class="icon" :data-id="item" data-target="icon" :data-icon="1" :class="{open: propertyObj[item].open, text: !relativeObj[item] || !relativeObj[item].length}"></div>
                    <div class="title" :data-id="item">{{propertyObj[item].id}}－</div>
                    <div class="title" :data-id="item">{{propertyObj[item].name}}</div>
                    <div class="icon settings" data-target="setting" :data-id="item" :data-key="key"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
	import './index.less';
    import Index from './index.js';
    export default Index;
</script>


