import Vue from 'vue';
import { mapState } from 'vuex';

import FcHeaderStore from './store';

const FcHeader =  {
    name: 'FcHeader',
    data() {
		return {};
	},
    computed: Object.assign ({
            
        }, mapState({
            userInfo: (state) => state.FcHeader.userInfo,
        homePage: (state) => state.config.homePage['index'],
		})
	),
    created() {
        if (!this.$store.state.FcHeader) {
            this.$store.registerModule('FcHeader', FcHeaderStore);
        }
    },
    methods: {

    }
}

export default FcHeader;


