import FcHeader from 'src/components/public/header/index.vue';
import FcMenu from 'src/components/public/menu/index.vue';

const Router =  {
    name: 'Router',
    components: {
        FcHeader,
        FcMenu
    },
    data() {
        return {
            showMenu: true
        };
    },
    created() {
        this.resize();
        window.onresize = () => {
            this.resize();
        };
    },
    methods: {
        resize() {
            if (this.rTo) {
                clearTimeout(this.rTo);
            }
            this.rTo = setTimeout(() => {
                const menuView = this.$refs.menuView;
                const routerView = this.$refs.routerView;
                const clientHeight = document.getElementById('app').clientHeight;
                let minHeight = clientHeight - 81;
                if (menuView.clientHeight > minHeight || routerView.clientHeight > minHeight) {
                    minHeight = menuView.clientHeight > routerView.clientHeight ? menuView.clientHeight : routerView.clientHeight;
                }
                menuView.style.height = routerView.style.height = minHeight + 'px';
            });
        },
        toggleMenu() {
            if (this.togging) {
                return;
            }
            this.togging = true;
            this.showMenu = !this.showMenu;
            const left = this.showMenu ? 0 : '-240px';
            const paddingLeft = this.showMenu ? '240px' : 0;
            const menuView = this.$refs.menuView;
            const routerView = this.$refs.routerView;
            menuView.style.left = left;
            routerView.style.paddingLeft = paddingLeft;
            if (this.to) {
                clearTimeout(this.to);
            }
            this.to = setTimeout(() => {
                this.togging = false;
            }, 400);
        }
    }
}

export default Router;


